
const staff = {
    state: {
        departInforList: [],//对应部门列表信息
        departNameSave: [],//所有部门名称的集合
        departId: "",//部门id
        pageSize: 10,  //分页条数
    },

    mutations: {
        GET_STAFF_LIST: (state, departInforList) => {
            state.departInforList = departInforList;
        },
        CHANGE_STAFF_ID: (state, departId) => {
            state.departId = departId;
        },
        GET_NAME_ARR: (state, departNameSave) => {
            state.departNameSave = departNameSave;
        },
        CHANGE_STAFF_PAGESIZE: (state, pageSize) => {
            state.pageSize = pageSize;
        }
    },

    actions: {}
};

export default staff;



