
const login = {
    state: {
        shopinfo: [],  //店铺信息  
        userid: "",   //userid
        shopid: "",   //shopid
    },

    mutations: {
        CHANGE_SHOPINFO: (state, shopinfo) => {
            state.shopinfo = shopinfo;
        },
        CHANGE_USERID: (state, userid) => {
            state.userid = userid;
        },
        SHOP_USERID: (state, shopid) => {
            state.shopid = shopid;
        },
    },

    actions: {}
};

export default login;



